@charset "utf-8";

#main.guide {
  .guideItem {
    margin-bottom: 80px;
  }

  .guideTitle {
    margin-top: 60px;
    margin-bottom: 16px;
    font-size: 22px;
    font-weight: 600;
    color: var(--main-color1);

    &::before {
      content: "";
      display: inline-block;
    }
  }

  .heading h2 {
    &.tit {
      line-height: 1;
      font-size: 30px;
      font-weight: 400;
    }

    &.tit2 {
      font-size: 26px;
      font-weight: 400;
    }
  }

  .sort_list {
    margin-left: 32px;
    display: flex;
    gap: 24px;

    > li {
      font-size: 16px;
      position: relative;
    }

    label {
      margin-right: 12px;
      color: var(--bk-color2);
      vertical-align: top;
      display: inline-block;

      + a {
        padding-right: 16px;
        overflow: hidden;
        white-space: nowrap;

        &::after {
          content: "";
          position: absolute;
          top: 10px;
          right: 0;
          border: 4px solid transparent;
          border-top: 4px solid var(--bk-color1);
        }

        &.active::after {
          top: 6px;
          -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
        }
      }
    }

    ul {
      width: 100%;
      max-height: 144px;
      padding: 8px 16px 16px;
      background-color: #fff;
      position: absolute;
      top: 32px;
      left: 0;
      z-index: 1;
      overflow-y: auto;
      vertical-align: top;
      box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.06);
      border-radius: 6px;
      display: none;
    }

    .active + ul {
      display: inline-block;
    }

    ul {
      &::-webkit-scrollbar {
        width: 6px;
      }

      &::-webkit-scrollbar-thumb {
        background: transparent;
        background-clip: padding-box;
        border: 4px solid var(--br-color1);
        border-radius: 12px;
      }

      li {
        margin-top: 8px;
        font-size: 15px;

        a:hover {
          text-decoration: underline;
        }
      }
    }
  }

