  caption {
    font-size: 0;
    text-indent: -999px;
    visibility: hidden;
  }

  table {
    th {
      text-align: left;
      font-size: 15px;
      font-weight: 400;
      background-color: #fff;
      border-bottom: 1px solid var(--br-color1);
    }

    tbody td {
      font-size: 15px;
      border-bottom: 1px solid var(--br-color1);
    }

    .underline {
      text-decoration: underline;
    }
  }

  .tb_prd {
    thead th {
      padding-bottom: 16px;
      font-size: 15px;
      color: var(--bk-color2);
    }

    tbody {
      td {
        height: 130px;
        padding: 22px 8px 22px 0;
      }

      .order {
        .date {
          font-size: 14px;
          display: block;
        }

        .underline {
          margin-top: 2px;
          font-size: 14px;
        }
      }
    }
  }

  .tb_list {
    border-top: 1px solid var(--bk-color2);
    table-layout: fixed;

    thead th {
      height: 62px;
      text-align: center;
      font-size: 14px;
      font-weight: 500;
      color: var(--bk-color2);
    }

    tbody {
      td {
        height: 64px;
        text-align: center;

        &.subject {
          padding-left: 32px;
          text-align: left;

          a {
            max-width: 80%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
        }
      }

      tr:last-child td {
        border-color: var(--bk-color2);
      }
    }
  }

  .tb_grid {
    border-top: 1px solid var(--br-color1);

    tbody {
      th {
        /* padding-top: 16px;
        vertical-align:top; */
      }

      td {
        height: 56px;
        /* padding-top: 14px;
        padding-bottom:16px; */
      }
    }
  }

  .tb_block {
    display: block;
    border: 0;

    tbody,
    tr,
    th,
    td {
      display: block;
      border: 0;
    }

    tr + tr {
      margin-top: 20px;
    }

    th {
      margin-bottom: 10px;
      font-size: 14px;
      font-weight: 500;
      color: var(--bk-color2);

      &.required::after {
        content: "*";
        margin-left: 4px;
        font-size: 16px;
        font-weight: 700;
        color: var(--main-color1);
      }
    }

    td {
      position: relative;
      word-break: keep-all;
    }
  }

  /* login */
  .login_input {
    width: 400px;
    height: 60px;
    position: relative;
    margin-bottom: 16px;

    input {
      width: 100%;
      height: 100%;
      padding-left: 125px;
      background-color: var(--bg-color2);
      border: 1px solid var(--br-color2);
      border-radius: 10px;
      position: absolute;
      color: var(--bk-color3, #999);
      font-size: 14px;
      font-weight: 350;
    }

    h5 {
      height: 100%;
      padding: 20px 0 20px 24px;
      position: absolute;
      color: var(--bk-color2, #666);
      font-size: 12px;
      font-weight: 500;
    }
  }
}


